.wrapper {
  max-width: 1070px;
  margin: 0 auto;
  padding: 0 10px;
}

html {
  width: 100%;
  height: 100%;
}

html body {
  padding-top: 50px;
  width: 100%;
  height: 100%;
}

html body .wrapper .slider {
  margin: 0 auto;
  height: 300px;
  width: 500px;
  position: relative;
  overflow: hidden;
}

html body .wrapper .slider img {
  position: absolute;
  width: 100%;
  left: 100%;
}

html body .wrapper .slider .show {
  left: 0;
}

html body .wrapper .button {
  margin: 0 auto;
  width: 150px;
  margin-top: 10px;
}